import React from 'react';
import ReactDOM from 'react-dom';
import { HashRouter } from 'react-router-dom';
import App from './App';
import { importWc } from './import-wc';
import './index.css';
import { setup } from './setup';

(async () => {
  await importWc();
  await setup();

  ReactDOM.render(
    <React.StrictMode>
      <HashRouter>
        <App />
      </HashRouter>
    </React.StrictMode>,
    document.getElementById('root'),
  );
})();
